import React,{Component} from 'react';
import {Header} from 'react-mdl'
class Navigate extends Component{
    render(){
        return(
            <div style={{position: 'relative'}}>

        <Header title={<span>Expense Manger </span>}>
        </Header>
</div>
        )
    }
}
export default Navigate;



